* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background: #fff;
  color: #444;
  font-family: 'Roboto', sans-serif;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

a {
  color: #9983F0;
}

p {
  font-size: 1.6rem;
  margin: 0 0 2rem 0;

  strong {
    color: #9983F0;
  }

  &.note {
    border-left: 1px solid #444;
    font-size: 1.4rem;
    font-style: italic;
    margin: 0 4rem;
    padding: 0 0 0 2rem;
  }
}

blockquote {
  font-family: 'Playfair Display', Serif;
  font-size: 2rem;
  font-style: italic;
  font-weight: 900;
  margin: 6rem auto;
  text-align: center;
  width: 80%;

  strong {
    color: #9983F0;
  }

  @media screen and (min-width:768px) {
    font-size: 3rem;
    margin: 12rem auto;
  }

  @media screen and (min-width:1280px) {
    font-size: 3rem;
    margin: 12rem auto;
    width: 70%;
  }
}

.block {
  display: block;
  margin: 0 1rem 6rem 1rem;
  max-width: 120rem;
  padding: 0 1rem;
  position: relative;

  @media screen and (min-width:768px) {
    margin: 0 auto 6rem auto;    
    padding: 0 10rem;
  }
}

.block-title {
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1;
  margin: 0 0 2rem 0;
  padding: 0;
  text-transform: uppercase;

  strong {
    color: #9983F0;
    font-size: 4rem;
    font-weight: 900;
  }

  @media screen and (min-width:768px) {
    font-size: 2.6rem;

    strong {
      font-size: 6rem;
    }
  }
}

.block-subtitle {
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 1;
  margin: 0 0 2rem 0;
  padding: 0;
  text-transform: uppercase;

  strong {
    color: #9983F0;
    font-size: 3.2rem;
    font-weight: 900;
  }

  @media screen and (min-width:768px) {
    font-size: 2rem;

    strong {
      font-size: 5rem;
    }
  }
}

.block-intro {
  @media screen and (min-width:768px) {
    height: 60rem;
    max-height: 80vh;
  }

  .image {
    background: transparent url('/app/assets/picture.jpg') center center no-repeat;
    background-size: cover;
    height: 70vh;
    margin: 0 -20px;
    width: auto;

    @media screen and (min-width:768px) {
      bottom: 0;
      display: block;
      height: auto;
      left: 0;
      margin: 0;
      position: absolute;
      right: auto;
      top: 0;
      width: 80%;
      z-index: 1;
    }
  }

  .text {
    display: block;
    margin-top: -6rem;

    @media screen and (min-width:768px) {
      top: 5rem;
      height: auto;
      margin-top: 0;
      max-width: 30%;
      right: 2.5rem;
      position: absolute;
      z-index: 2;
    }

    h1 {
      background: #fff;
      box-shadow: 0 0 0 .4rem #fff;
      margin: 0 0 2rem 0;
    }

    h2 {
      background: #fff;
      box-shadow: 0 0 0 4px #fff;
      font-size: 2rem;
      font-weight: 300;
      line-height: 1.25;
      margin: 0 0 2rem 0;
      padding: 0;
      text-transform: uppercase;
    }

    p {
      background: #fff;
      box-shadow: 0 0 0 4px #fff;
      font-size: 1.4rem;
      font-weight: 300;
      margin: 0 0 2rem 0;
      padding: 0;

      &.gallery {
        background: transparent;
        box-shadow: none;
      }

      strong {
        color: #9983F0;
      }

      em {
        font-style: normal;
        font-weight: 400;
        text-transform: uppercase;
      }
    }

    img {
      height: 6rem;
      object-fit: contain;
      width: 6rem;
    }
  }
}

.block-chart {}




.block-chart-beeswarm {
  height: 55rem;
  padding: 0;

  .block-title {
    margin-left: 1.5rem;
  }

  @media screen and (min-width:768px) {
    height: 40rem;
    padding: 0 8.5rem;
  }

  .beeswarm-container {
    display: block;
    filter: blur(0);
    height: 55rem;
    margin: 0;
    position: relative;
    transition: filter .25s ease-in-out;
    width: 100%;

    @media screen and (min-width:768px) {
      height: 40rem;
    }
  
    &.blurred {
      filter: blur(10px);
    }
  
    .beeswarm-tick text {
      font-size: 1.2rem;
    }
  
    .beeswarm-word {
      fill: none;
      stroke: #9983F0;
      stroke-width: 1.5;
    }
  
    .icon {
      font-size: 30px;
    }
  
    .text-xl {
      fill: #F17BD7;
      font-size: 1.8rem;
      font-weight: 900;
      letter-spacing: .025em;
      text-transform: uppercase;
    }
  
    .text-s {
      fill: #F17BD7;
      font-size: 1rem;
      font-weight: 900;
      letter-spacing: .025em;
      text-transform: uppercase;
    }
  }

  .words-inner-tooltip {
    display: flex;
    min-height: 30px;
    min-width: 250px;
    position: relative;
  
    h2 {
      color: #F17BD7;
      font-size: 50px;
      font-weight: 900;
      line-height: 1;
      margin: 0;
      padding: 0;
      z-index: 1;
    }
    
    p {
      color: #fff;
      margin-top: 5px;
      padding-left: 10px;
    }
  
    img {
      height: 50px;
      object-fit: contain;
      width: 50px;
    }
  }
}

.grid-wrapper {
  display: block;
  margin-bottom: 6rem;

  @media screen and (min-width:768px) {
    display: flex;
    margin: 0 -1rem 6rem -1rem;;
  }

  .grid-cell {
    display: block;
    margin-bottom: 2rem;

    @media screen and (min-width:768px) {
      flex: 0 0 33.33%;
      padding: 0 1rem;
    }

    h3 {
      font-size: 3rem;
      font-weight: 900;
      line-height: 1;
      margin: 0 0 2rem 0;
      text-align: center;

      @media screen and (min-width:768px) {
        font-size: 3.6rem;
        text-align: left;
      }
    }

    p {
      text-align: center;

      @media screen and (min-width:768px) {
        text-align: left;
      }
    }

    &.total-words h3 {
      color: #F17BD7;
    }

    &.noStopwords-words h3 {
      color: #FF83AC;
    }

    &.noOnomatopoeic-words h3 {
      color: #FFA381;
    }
  }
}