.points-container {
  display: block;
  height: auto;
  margin: 60px auto;
  position: relative;
  width: 300px;

  @media screen and (min-width: 768px) {
    margin: 120px auto;
    width: 500px;
  }

  @media screen and (min-width: 1280px) {
    width: 900px;
  }

  .points-row {
    display: block;
    height: auto;
    position: sticky;
    top: 120px;
    width: 100%;

    @media screen and (min-width:768px) {
      display: flex;
      top: 180px;
    }
  }

  .points-explanation {
    display: block;
    font-size: 1rem;
    font-weight: 200;
    height: 10rem;
    line-height: 2rem;
    margin: 0 auto;
    padding-top: 0;
    position: relative;
    text-transform: uppercase;
    white-space: nowrap;
    width: 250px;

    @media screen and (min-width:768px) {
      flex: 0 0 200px;
      font-size: 1.2rem;
      padding: 20px 0 0 20px;
    }

    @media screen and (min-width:1280px) {
      flex: 0 0 300px;
      font-size: 1.4rem;
    }

    span:before {
      border: 2px solid rgba(0,0,0,.25);
      border-radius: 50%;
      content: '';
      display: inline-block;
      height: 6px;
      margin-right: 5px;
      vertical-align: middle;
      width: 6px;
    }
  }

  .points-row-1 {
    .points-explanation {
      span:before {
        border-color: rgba(0,0,0,.5);
      }
    }
  }

  .points-row-2 {
    .points-explanation {

      span:before {
        border-color: rgba(0,0,0,.75);
      }
    }
  }

  .points-row-3 {
    .points-explanation {

      span:before {
        background-color: red;
        border-color: red;
      }
    }
  }
  
  .points-wrapper {
    display: block;
    height: 250px;
    margin: 10px auto;
    position: relative;
    width: 250px;

    @media screen and (min-width:768px) {
      flex: 0 0 300px;
      height: 300px;
      width: 300px;
    }

    @media screen and (min-width:1280px) {
      flex: 0 0 600px;
      height: 600px;
      width: 600px;
    }
  }
}