.beeswarm {
  display: block;
  height: 100%;
  position: relative;
  width: 100%;

  svg {
    z-index: 1;
  }

  .beeswarm-highlighter {
    background: #fff;
    bottom: 0;
    box-shadow: 2px 0 0 2px #fff, -2px 0 0 2px #fff;
    display: block;
    left: 0;
    mix-blend-mode: multiply;
    height: auto;
    opacity: 0;
    position: absolute;
    top: 0;
    transition: opacity .25s ease-in-out;
    width: 1px;
    z-index: 99;

    &:after {
      background: #9983F0;
      bottom: 0;
      content: '';
      height: auto;
      left: 50%;
      mix-blend-mode: multiply;
      position: absolute;
      top: 0;
      transform: translate3d(-50%, 0, 0);
      width: 1px;
    }
  }

  .beeswarm-axis-custom {
    font-size: 1rem;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }

  .beeswarm-tooltip {
    background: #9983F0 ; //rgba(255, 255, 255, .85);
    border-top-right-radius: 10px;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, .1);
    display: block;
    font-size: 1.4rem;
    height: auto;
    left: 0;
    max-width: 300px;
    opacity: 0;
    padding: 5px 10px;
    position: absolute;
    transform: translate3d(10px, -10px, 0);
    transition: opacity .25s ease-in-out;
    z-index: 100;

    &:after {
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent; 
      border-right: 6px solid #9983F0; 
      bottom: 10px;
      content: '';
      display: block;
      height: 0;
      left: -6px;
      position: absolute;
      width: 0;
    }

    &.reverse {
      border-top-right-radius: 0;
      border-top-left-radius: 10px;
      transform: translate3d(calc(-100% - 10px), -10px, 0);

      &:after {
        border-top: 6px solid transparent;
        border-bottom: 6px solid transparent; 
        border-left: 6px solid #9983F0; 
        border-right: none;
        left: auto;
        right: -6px;
      }
    }
  }
}